.character-card {
    background: salmon;
    text-align: center;
    height: 350px;
    width: 200px;
    border: 1px solid black;
}

.link {
    text-decoration: none;
}

.character-image {
    width: 100%;
    height: 250px;
    -o-object-fit: cover;
       object-fit: cover;
    -o-object-position: top;
       object-position: top;
}

.character-name {
    font-size: 20px;
    color: white;
}

.character-house {
    font-size: 15px;
    color: white;
    font-weight: normal;
}


